@import "../base";

.c-page--password {
  height: 100vh;
}

// Hide Announcement bar
#epb_bar {
  display: none;
}

// Hide Rewards Smile overlay
.smile-launcher-frame-container.smile-launcher-border-radius-circular {
  display: none;
}

.c-template-password {
  position: relative;
  overflow: visible;
  padding: 0 $s-gutter--edge-mobile;
  margin-top: 0;
  top: 50vh;
  transform: translateY(-50%);
  margin: 0 auto;
  text-align: center;
  max-width: 30rem;

  &__wrapper {
    text-align: center;
    margin-bottom: 2rem;
  }

  &__heading {
    margin-bottom: 1rem;
  }

  &__subheading {
    margin: 0.5rem 0;
  }

  &__password {
    margin-bottom: 3rem;

    a {
      color: $s-color--font-focus;
      position: relative;

      &::before {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 0.0625rem;
        background: $s-color--border-focus;
        content: '';
      }
    }
  }

  &__input {
    margin-bottom: 1rem;
  }

  &__register-title {
    margin-bottom: 0;
  }

  @include t-media-query($s-medium-up) {
    max-width: 40rem;

    &__heading {
      margin-bottom: 2rem;
    }
  }
}